.footer__wrap {
    background-color: $bg-color;
    padding: 350px 0 80px;
    position: relative;
}
.footer {
    padding: 0 30px;
}
.footer__form {
    margin: 0 auto;
    display: grid;
    gap: 36px;
    background-color: $white;
    max-width: max-content;
    padding: 70px 150px;
    border-radius: 8px;
    box-shadow: 2px 8px 16px rgba(194, 198, 208, 0.06);
    border: 2px solid #E2EAF2;
    box-sizing: border-box;

    form {
        display: flex;
        gap: 20px;
        justify-content: center;
    }

    .form-phone {
        background-color: $gray;
        border: none;
        padding: 32px 58px;
        width: 380px;
        border-radius: 8px;
    }

    .btn {
        border: none;
    }
}

.footer__form-title {
    display: grid;
    gap: 12px;
    text-align: center;
    h2 {
        font-size: px-to-rem(36px);
        line-height: px-to-rem(47px);
    }

    p {
        font-size: px-to-rem(21px);
        line-height: px-to-rem(30px);
    }

}

.footer__documents {
    padding: 80px 0 0;
    display: grid;
    gap: 20px;
    text-align: center;
    color: $gray-4;
    a {
        color: $gray-4;
        &:hover {
            color: $gray;
        }
    }

    hr {
        margin: 0;
        border: 0.1px solid #7E868E;
        opacity: 0.1;
    }
}

.nav {
    display: flex;
    gap: 50px;
    justify-content: center;
}

.kerndigital:hover {
    text-decoration: underline;
}

.footer__berry-right {
    position: absolute;
    width: 140px;
    top: 20%;
    right: 5%;
}

.footer__leaf-right {
    position: absolute;
    bottom: 10%;
    right: 5%;
}

.footer__berry-left {
    position: absolute;
    bottom: 20%;
    width: 150px;
    left: 5%;
}

.footer__leaf-left {
    position: absolute;
    top: 15%;
    left: 5%;
}

@media (max-width:1700px) {
    .footer__wrap {
        padding: 250px 0 80px;
    }
}

@media (max-width:1300px) {

    .footer__form {

        gap: 36px;
        padding: 50px;
    
        form {
            display: flex;
            gap: 15px;
            justify-content: center;
        }
    
        .form-phone {
            padding: 20px 30px;
            width: 300px;
        }
    
        .btn {
            padding: 20px 30px;
        }
    }

    .footer__form-title {
        h2 {
            font-size: px-to-rem(28px);
            line-height: px-to-rem(36px);
        }
    
        p {
            font-size: px-to-rem(18px);
            line-height: px-to-rem(28px);
        }
    
    }
}

@media (max-width:750px) {
    .footer__wrap {
        padding: 200px 0 80px;
    }

    .footer__form {
        gap: 30px;
    
        form {
            flex-direction: column;
            align-items: center;
        }

    }

    .footer__form-title {
        max-width: 340px;
        margin: 0 auto;

        h2 {
            font-size: px-to-rem(24px);
            line-height: px-to-rem(32px);
        }
    
        p {
            font-size: px-to-rem(16px);
            line-height: px-to-rem(28px);
        }
    
    }

    .footer__documents {
        padding: 60px 0 0;
        gap: 25px;
    }

    .nav {
        flex-direction: column;
        gap: 15px;
    }
}

@media (max-width:500px) {

    .footer__wrap {
        padding: 150px 0 80px;
    }

    .footer__form {

        padding: 50px 30px;
    
        .form-phone {
            max-width: 200px;
            padding: 15px 20px;
            font-size: px-to-rem(16px);
            line-height: px-to-rem(26px);
        }

        .btn {
            font-size: px-to-rem(16px);
            line-height: px-to-rem(26px);
            padding: 15px 20px;
        }
    }

    .footer__form-title {

        h2 {
            font-size: px-to-rem(20px);
            line-height: px-to-rem(28px);
        }
    
        p {
            font-size: px-to-rem(12px);
            line-height: px-to-rem(20px);
        }
    
    }

}