.reviews__wrap {
    background-color: $bg-color;
    position: relative;

    .section__title {
        color: $white;
        padding-top: 300px;
        padding-bottom: 0;
    }
}

.review__slider {
    padding-bottom: 250px;
    padding-top: 80px;
}

.review__slide {
    background-color: $white;
    display: flex;
    gap: 24px;
    max-width: max-content;
    padding: 70px;
    align-items: flex-start;
    border-radius: 8px;
    box-shadow: 2px 8px 16px rgba(194, 198, 208, 0.06);
    border: 2px solid #E2EAF2;
    box-sizing: border-box;
}

.review__slide-text {
    max-width: 460px;
    display: grid;
    gap: 24px;

    h3 {
        font-weight: 500;
        font-size: px-to-rem(30px);
        line-height: px-to-rem(43px);
    }

    p {
        font-size: px-to-rem(15px);
        line-height: px-to-rem(25px);
    }

    img {
        width: 30px;
    }
}

.review__slide-name-img {
    text-align: center;
    display: grid;
    gap: 30px;
    justify-items: center;
}

.review__slide-photo {
    width: 300px;
    img {
        width: 100%;
        border-radius: 50%;
    }
}

.review__slide-name {

    h4 {
        font-weight: 500;
        font-size: px-to-rem(21px);
        line-height: px-to-rem(30px);
    }

    p {
        color: $gray-middle;
    }
}

.swiper-slide-active {
    -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    -webkit-transform: translate3d(0,20px,0);
    transform: translate3d(0,20px,0);    
} 

.swiper-slide-prev , .swiper-slide-next{
    opacity: 0.3;
    -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    -webkit-transform: translate3d(0,20px,0);
    transform: translate3d(0,20px,0);    
}

.review__berry-right {
    position: absolute;
    top: 30%;
    right: 10%;
    width: 140px;
    z-index: 100;
}
.review__leaf {
    position: absolute;
    bottom: 5%;
    left: 10%;
    z-index: 100;
}

@media (max-width:1200px) { 

    .reviews__wrap {
    
        .section__title {
            padding-top: 150px;
        }
    }

    .review__slider {
        padding-bottom: 150px;
        padding-top: 60px;
    }

    .review__slide {
        padding: 50px;
    }

    .review__slide-text {
        max-width: 370px;
    
        h3 {
            font-size: px-to-rem(24px);
            line-height: px-to-rem(30px);
        }

        p {
            font-size: px-to-rem(13px);
            line-height: px-to-rem(22px);
        }
    
        img {
            width: 20px;
        }
    }

    .review__slide-photo {
        width: 200px;
    }

    .review__slide-name {

        h4 {
            font-size: px-to-rem(18px);
            line-height: px-to-rem(26px);
        }

        p {
            font-size: px-to-rem(14px);
        }

    }

}

@media (max-width:780px) { 

    .review__slide {
        flex-direction: column;
        text-align: center;
        align-items: center;
        gap: 20px;
    }

    .review__slide-text {
        max-width: 300px;
        gap: 20px;
    
        h3 {
            font-weight: 500;
            font-size: px-to-rem(20px);
            line-height: px-to-rem(28px);
        }

        p {
            font-size: px-to-rem(12px);
            line-height: px-to-rem(20px);
        }
    
        img {
            width: 20px;
        }
    }

    .review__slide-name-img {
        gap: 20px;
    }

    .review__slide-name {

        h4 {
            font-size: px-to-rem(16px);
            line-height: px-to-rem(26px);
        }

        p {
            font-size: px-to-rem(13px);
        }

    }

    .review__slide-photo {
        width: 150px;
    }

}

@media (max-width:460px) { 

    .reviews__wrap  {

        .section__title {
            padding-top: 60px; 
        }
    }

    .review__slider {
        padding: 50px 30px 100px 30px;
    }

    .review__slide {
        padding: 20px;
    }

}

