.gallery__wrap {
    padding-bottom: 20px;

    .section__title {
        display: grid;
        gap: 10px;

        p {
            font-size: px-to-rem(24px);
            line-height: px-to-rem(34px);
        }
    }
}
.gallery {
    display: grid;
    gap: 15px;
    grid-template-areas: 
        'img-1 img-2 img-3'
        'img-4 img-5 img-6'
        'img-7 img-8 img-9'
        ;
    justify-content: center;
    img {
        width: 100%;
    }

    .gallery__img {
        transition: .5s linear;

        &:hover {
            transform: scale(1.04);
            transition: .5s linear;
        }
    }
}

.gallery__img:nth-of-type(1) {
    grid-area: img-1;
}
.gallery__img:nth-of-type(2) {
    grid-area: img-2;
}
.gallery__img:nth-of-type(3) {
    grid-area: img-3;
}
.gallery__img:nth-of-type(4) {
    grid-area: img-4;
}
.gallery__img:nth-of-type(5) {
    grid-area: img-5;
}
.gallery__img:nth-of-type(6) {
    grid-area: img-6;
}
.gallery__img:nth-of-type(7) {
    grid-area: img-7;
}
.gallery__img:nth-of-type(8) {
    grid-area: img-8;
}
.gallery__img:nth-of-type(9) {
    grid-area: img-9;
}

@media (max-width:1200px) {
    .gallery__wrap {

        .section__title {
    
            p {
                font-size: px-to-rem(18px);
                line-height: px-to-rem(28px);
            }
        }
    }
}

@media (max-width:780px) { 

    .gallery {
        grid-template-areas: 
        'img-1 img-2'
        'img-4 img-5'
        'img-8 img-8'
        'img-7 img-3'
        'img-6 img-9'
        ;
    }
}

@media (max-width:425px) { 

    .gallery {
        gap: 6px;
        grid-template-areas: 
        'img-1 img-1'
        'img-2 img-4'
        'img-5 img-5'
        'img-7 img-3'
        'img-8 img-8'
        'img-6 img-9'
        ;
    }

    .gallery__wrap {

        .section__title {
    
            p {
                font-size: px-to-rem(16px);
                line-height: px-to-rem(26px);
            }
        }
    }
}