@charset "UTF-8";
/* Указываем box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.3;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
  list-style: none;
}

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
}

/**
 * Основные стили
 **/
html {
  font-size: 16px;
  color: #2E363E;
}

body {
  font-family: "Montserrat", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
  font-weight: 400;
}

a {
  text-decoration: none;
}

.btn {
  text-decoration: none;
  box-sizing: border-box;
  text-align: center;
  color: #2E363E;
  border-radius: 8px;
  font-weight: 500;
  width: max-content;
  padding: 30px 50px;
  font-size: 1.125rem;
  display: inline-block;
  background-color: #FED84C;
}
.btn:hover {
  background-color: #e3b301;
  text-decoration: none;
}

.subtitle {
  background-color: #F2F6FA;
  width: max-content;
  color: #2E363E;
  font-size: 0.6875rem;
  padding: 10px 18px;
  border-radius: 5px;
  cursor: default;
}

.container {
  min-width: 320px;
