.footer__wrap {
  background-color: #081130;
  padding: 350px 0 80px;
  position: relative;
}

.footer {
  padding: 0 30px;
}

.footer__form {
  margin: 0 auto;
  display: grid;
  gap: 36px;
  background-color: #fff;
  max-width: max-content;
  padding: 70px 150px;
  border-radius: 8px;
  box-shadow: 2px 8px 16px rgba(194, 198, 208, 0.06);
  border: 2px solid #E2EAF2;
  box-sizing: border-box;
}
.footer__form form {
  display: flex;
  gap: 20px;
  justify-content: center;
}
.footer__form .form-phone {
  background-color: #F2F6FA;
  border: none;
  padding: 32px 58px;
  width: 380px;
  border-radius: 8px;
}
.footer__form .btn {
  border: none;
}

.footer__form-title {
  display: grid;
  gap: 12px;
  text-align: center;
}
.footer__form-title h2 {
  font-size: 2.25rem;
  line-height: 2.9375rem;
}
.footer__form-title p {
  font-size: 1.3125rem;
  line-height: 1.875rem;
}

.footer__documents {
  padding: 80px 0 0;
  display: grid;
  gap: 20px;
  text-align: center;
  color: #7E868E;
}
.footer__documents a {
  color: #7E868E;
}
.footer__documents a:hover {
  color: #F2F6FA;
}
.footer__documents hr {
  margin: 0;
  border: 0.1px solid #7E868E;
  opacity: 0.1;
}

.nav {
  display: flex;
  gap: 50px;
  justify-content: center;
}

.kerndigital:hover {
  text-decoration: underline;
}

.footer__berry-right {
  position: absolute;
  width: 140px;
  top: 20%;
  right: 5%;
}

.footer__leaf-right {
  position: absolute;
  bottom: 10%;
  right: 5%;
}

.footer__berry-left {
  position: absolute;
  bottom: 20%;
  width: 150px;
  left: 5%;
}

.footer__leaf-left {
  position: absolute;
  top: 15%;
  left: 5%;
}

@media (max-width: 1700px) {
  .footer__wrap {
    padding: 250px 0 80px;
  }
